export default function (path) {
  return path.length === 1 ? get1(path[0]) : getN(path);
}

var get1 = function get1(field) {
  return function (obj) {
    return obj[field];
  };
};

var getN = function getN(path) {
  var len = path.length;
  return function (obj) {
    for (var i = 0; i < len; ++i) {
      obj = obj[path[i]];
    }

    return obj;
  };
};